<script>
    export default {
        name: "vehica-templates-list",
        data() {
            return {
                templates: [],
                activeTemplate: 0,
                globalLayoutId: 0,
            }
        },
        props: {
            savingChangesText: String,
            changesSavedText: String,
            setGlobalLayoutRequestUrl: String,
            createRequestUrl: String,
            deleteRequestUrl: String,
            setActiveRequestUrl: String,
            duplicateRequestUrl: String,
            duplicateInProgressText: String,
            duplicateSuccessText: String,
            creatingTemplateText: String,
            deletingTemplateText: String,
            templateNameText: String,
            createSuccessText: String,
            deleteSuccessText: String,
            templateType: String,
            areYouSureTitleText: String,
            areYouSureMsgText: String,
            confirmText: String,
            cancelText: String,
            initialTemplates: {
                type: Array,
                default: () => {
                    return []
                }
            },
            initialActiveTemplate: 0,
            initialGlobalLayoutId: Number,
        },
        methods: {
            duplicate(template) {
                swal({
                    title: this.duplicateInProgressText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.duplicateRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {postId: template.id},
                        success: (response) => {
                            if (!response || !response.hasOwnProperty('success') || !response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }
                            window.history.replaceState({}, '', window.location.href)

                            this.templates.push(response.post)

                            swal({
                                title: this.duplicateSuccessText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            setGlobalLayout(template) {
                this.globalLayoutId = template.id

                swal({
                    title: this.savingChangesText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.setGlobalLayoutRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {layoutId: template.id},
                        success: () => {
                            window.history.pushState({}, '', window.location.href)

                            swal({
                                title: this.changesSavedText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            setActiveTemplate(template) {
                this.activeTemplate = template.id

                swal({
                    title: this.savingChangesText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.setActiveRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {templateId: template.id, templateType: this.templateType},
                        success: () => {
                            window.history.pushState({}, '', window.location.href)

                            swal({
                                title: this.changesSavedText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            onDeleteTemplate(template) {
                swal.fire({
                    title: this.areYouSureTitleText,
                    text: this.areYouSureMsgText,
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: this.confirmText,
                    cancelButtonText: this.cancelText
                }).then((result) => {
                    if (result.value) {
                        this.deleteTemplate(template)
                    }
                })
            },
            deleteTemplate(template) {
                swal({
                    title: this.deletingTemplateText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.deleteRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {templateId: template.id},
                        success: (response) => {
                            if (!response.success) {
                                swal({
                                    title: response.message,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            let index = this.templates.findIndex((t) => {
                                return template.id === t.id
                            })

                            if (index !== -1) {
                                this.templates.splice(index, 1)
                            }

                            window.history.pushState({}, '', window.location.href)

                            this.checkActiveTemplate()

                            swal({
                                title: this.deleteSuccessText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            checkActiveTemplate() {
                if (this.templates.length === 0) {
                    return
                }

                let template = this.templates.find((template) => {
                    return template.id === this.activeTemplate
                })

                if (typeof template !== 'undefined') {
                    return
                }

                this.activeTemplate = this.templates[0].id
                if (this.templateType === 'layout') {
                    this.globalLayoutId = this.templates[0].id
                }
            },
            createTemplate() {
                swal({
                    title: this.creatingTemplateText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.createRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {
                            templateName: this.templateNameText,
                            templateType: this.templateType,
                        },
                        success: (response) => {
                            if (!response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            this.templates.push(response.template)

                            window.history.pushState({}, '', window.location.href)

                            this.checkActiveTemplate()

                            swal({
                                title: this.createSuccessText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            }
        },
        render() {
            return this.$scopedSlots.default({
                createTemplate: this.createTemplate,
                templates: this.templates,
                onDeleteTemplate: this.onDeleteTemplate,
                setActiveTemplate: this.setActiveTemplate,
                activeTemplate: this.activeTemplate,
                globalLayoutId: this.globalLayoutId,
                setGlobalLayout: this.setGlobalLayout,
                duplicate: this.duplicate,
            })
        },
        created() {
            this.templates = this.initialTemplates
            this.activeTemplate = this.initialActiveTemplate
            this.globalLayoutId = this.initialGlobalLayoutId

            window.VehicaAdminEvents.$on('mcTemplateUpdated', (template) => {
                let index = this.templates.findIndex((t) => {
                    return t.id === template.id
                })

                if (index !== -1) {
                    this.$set(this.templates, index, template)
                }
            })
        }
    }
</script>



