<script>
    export default {
        name: "vehica-sections",
        data() {
            return {
                fieldsUser: {},
                selectedTypes: {},
                validation: false,
                publishFlag: true,
            }
        },
        props: {
            fields: {
                type: Array,
                default: () => {
                    return []
                }
            },
            optFieldsUser: {
                type: Object
            },
            requiredFieldsMsg: {
                type: String,
                default: 'The required fields have not been filled out:'
            }
        },
        computed: {
            relations() {
                let relations = []
                jQuery.each(this.selectedTypes, (taxonomyId, terms) => {
                    jQuery.each(terms, (index, term) => {
                        jQuery.each(term.relations, (index, relation) => {
                            relations.push(relation)
                        })
                    })
                })
                return relations
            },
            visibleSections() {
                return this.sections.filter((section) => {
                    return section.fields.some((field) => {
                        return this.showField(field)
                    })
                })
            }
        },
        methods: {
            showField(field) {
                if (!this.relations.length) {
                    return true
                }

                if (typeof this.selectedTypes[field.id] !== 'undefined' && this.selectedTypes[field.id].length > 0) {
                    return true
                }

                return this.relations.some((relation) => {
                    let key = 'vehica_' + relation.termId + '_vehica_custom_field_' + field.id
                    if (key === relation.key) {
                        return relation.value
                    }
                    return false
                })
            },
            onUpdateCar() {
                this.validation = true
                let elements = jQuery('.vehica-field.is-required.is-empty')
                if (elements.length) {
                    let fields = jQuery.map(elements, (element) => {
                        return jQuery(element).data('name')
                    }).join(', ')
                    alert(this.requiredFieldsMsg + ' ' + fields)
                } else {
                    // jQuery(this.$el).closest('form').submit()
                    this.publishFlag = false
                    jQuery('#publishing-action #publish').click()
                }
            }
        },
        render() {
            return this.$scopedSlots.default({
                fieldsUser: this.fieldsUser,
                validation: this.validation,
                relations: this.relations,
                showField: this.showField,
                showTaxonomy: this.showTaxonomy,
                fields: this.fields,
            })
        },
        created() {
            this.fieldsUser = this.optFieldsUser

            window.VehicaAdminEvents.$on('taxonomyUpdated', (data) => {
                if (data.taxonomy.fieldsDependencyEnabled) {
                    this.$set(this.selectedTypes, data.taxonomy.id, data.terms)
                }
            })
        },
        mounted() {
            window.VehicaAdminEvents.$on('updateCar', () => {
                this.onUpdateCar()
            })

            jQuery('#publishing-action #publish').on('click', (e) => {
                if (this.publishFlag) {
                    e.preventDefault()
                    this.onUpdateCar()
                } else {
                    this.publishFlag = true
                }
            })
        }
    }
</script>



