<script>
    export default {
        name: "vehica-user-image",
        data() {
            return {
                image: false,
                uploader: false,
            }
        },
        props: {
            initialImageId: Number,
            titleText: String,
            buttonText: String,
        },
        computed: {
            hasImage() {
                return this.image !== false
            },
            imageUrl() {
                if (!this.hasImage) {
                    return ''
                }

                return this.image.url
            },
            imageId() {
                if (!this.hasImage) {
                    return 0
                }

                return this.image.id
            }
        },
        methods: {
            onSetImage() {
                if (this.uploader) {
                    this.uploader.open()
                    return
                }

                this.uploader = wp.media({
                    title: this.titleText,
                    button: {
                        text: this.buttonText
                    },
                    multiple: false
                })

                this.uploader.on('select', () => {
                    this.image = this.uploader.state().get('selection').first().toJSON()
                })

                this.uploader.open()
            },
            onRemoveImage() {
                this.image = false
            }
        },
        render() {
            return this.$scopedSlots.default({
                onSetImage: this.onSetImage,
                hasImage: this.hasImage,
                imageId: this.imageId,
                onRemoveImage: this.onRemoveImage,
                url: this.imageUrl,
            })
        },
        created() {
            if (this.initialImageId !== false) {
                wp.media.attachment(this.initialImageId).fetch().then(() => {
                    this.image = wp.media.attachment(this.initialImageId).toJSON()
                })
            }
        }
    }
</script>



