<script>
  export default {
    name: "vehica-image",
    data() {
      return {
        image: false,
        media: false,
        useUrl: false
      }
    },
    props: {
      optField: {
        type: Object
      },
      optFieldsUser: {
        type: Object
      },
      titleText: {
        type: String
      },
      buttonText: {
        type: String
      },
      validation: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      classes() {
        if (!this.optField.isRequired) {
          return {}
        }
        return {
          'is-required': true,
          'is-empty': this.image === false,
          'is-error': this.validation && this.image === false
        }
      },
    },
    methods: {
      deleteImage() {
        this.image = false
      },
      openMedia() {
        if (this.media) {
          this.media.open()
          return
        }

        this.media = wp.media({
          title: this.titleText,
          button: {
            text: this.buttonText
          },
          library: {
            type: 'image'
          }
        })
        this.media.on('open', () => {
          if (!this.image) {
            return
          }

          let selection = this.media.state().get('selection')
          let img = wp.media.attachment(this.image.id)
          img.fetch()
          selection.add(img ? [img] : [])
        })
        this.media.on('select', () => {
          this.media.state().get('selection').map((image) => {
            image = image.toJSON()

            this.$set(this, 'image', {
              id: image.id,
              srcset: {thumbnail: ''},
              url: image.sizes.thumbnail.url
            })
            this.useUrl = true
          })
        })
        this.media.open()
      }
    },
    render() {
      return this.$scopedSlots.default({
        field: this.optField,
        fieldsUser: this.optFieldsUser,
        value: this.value,
        image: this.image,
        openMedia: this.openMedia,
        deleteImage: this.deleteImage,
        classes: this.classes,
        useUrl: this.useUrl
      })
    },
    created() {
      jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
        if (attribute.id === this.optField.id) {
          this.image = attribute.value
        }
      })
    }
  }
</script>



