<script>
    export default {
        name: "vehica-delete-car-field",
        data() {
            return {
                inProgress: false
            }
        },
        props: {
            field: Object,
            successTitleText: String,
            errorTitleText: String,
            deletingFieldText: String,
            areYouSureTitleText: String,
            areYouSureMsgText: String,
            confirmText: String,
            cancelText: String,
            requestUrl: String,
        },
        methods: {
            onDeleteField() {
                swal.fire({
                    title: this.areYouSureTitleText,
                    text: this.areYouSureMsgText,
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: this.confirmText,
                    cancelButtonText: this.cancelText
                }).then((result) => {
                    if (result.value) {
                        this.deleteField()
                    }
                })
            },
            deleteField() {
                this.inProgress = true

                swal({
                    title: this.deletingFieldText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        data: {fieldId: this.field.id},
                        dataType: 'json',
                        success: (response) => {
                            this.inProgress = false

                            if (!response.success) {
                                swal({
                                    title: this.errorTitleText,
                                    text: response.message,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            window.VehicaAdminEvents.$emit('vehicaCarFieldDeleted', this.field)

                            swal({
                                title: this.successTitleText,
                                text: response.message,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            this.inProgress = false
                            swal({
                                title: this.errorTitleText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            }
        },
        render() {
            return this.$scopedSlots.default({
                deleteField: this.onDeleteField,
                inProgress: this.inProgress,
            })
        }
    }
</script>



