<script>
export default {
    name: "vehica-location",
    data() {
        return {
            address: '',
            position: false,
            field: false,
            map: false,
            marker: false,
            markerChangeAddress: true,
            zoom: 8,
        }
    },
    props: {
        optField: {
            type: Object
        },
        optFieldsUser: {
            type: Object
        },
        validation: {
            type: Boolean,
            default: false
        },
        snazzy: Boolean,
    },
    computed: {
        classes() {
            if (!this.optField.isRequired) {
                return {}
            }

            return {
                'is-required': true,
                'is-empty': this.address.length === 0,
                'is-error': this.validation && this.address.length === 0
            }
        },
    },
    methods: {
        initMap() {
            let elementId = 'vehica-map-' + this.field.id

            this.map = new google.maps.Map(document.getElementById(elementId), {
                center: this.position ? this.position : this.optField.initialPosition,
                zoom: this.optField.zoom,
                scrollwheel: false,
            })

            if (this.snazzy && typeof window.VehicaSnazzy !== 'undefined') {
                this.map.mapTypes.set('styled_map', new google.maps.StyledMapType(window.VehicaSnazzy))
                this.map.setMapTypeId('styled_map')
            } else {
                this.map.setMapTypeId(this.optField.mapType)
            }

            google.maps.event.addListener(this.map, 'click', (event) => {
                this.position = {
                    lat: event.latLng.lat(),
                    lng: event.latLng.lng()
                }
                this.setMarker()

                if (this.markerChangeAddress) {
                    this.setAddressFromPosition()
                }
            })
            let inputKey = 'vehica-address-' + this.field.id
            let textInput = document.getElementById(inputKey)
            let options = {
                types: [this.optField.inputType],
                componentRestrictions: {country: this.optField.countries},
            }
            let autoComplete = new google.maps.places.Autocomplete(textInput, options)
            autoComplete.addListener('place_changed', () => {
                let place = autoComplete.getPlace()
                if (!place.geometry) {
                    return
                }
                this.address = place.formatted_address
                this.position = {
                    lat: place.geometry.location.lat(),
                    lng: place.geometry.location.lng(),
                }
                this.map.setCenter(this.position)
                this.setMarker()
            })

            textInput.addEventListener('keydown', (event) => {
                if (event.key === 'Enter') {
                    event.preventDefault()
                }
            })

            this.setMarker()
        },
        setAddressFromPosition() {
            let geocoder = new google.maps.Geocoder
            geocoder.geocode({'location': this.position}, (results, status) => {
                if (status === 'OK') {
                    if (results[0]) {
                        this.address = results[0].formatted_address
                    }
                }
            })
        },
        setAddress(address) {
            this.address = address
        },
        setMarker() {
            if (!this.position) {
                return
            }

            if (this.marker) {
                this.marker.setMap(null)
            }
            this.marker = new google.maps.Marker({
                position: this.position,
                map: this.map
            })
        },
        setMarkerChangeAddress() {
            this.markerChangeAddress = !this.markerChangeAddress
        }
    },
    render() {
        return this.$scopedSlots.default({
            position: this.position,
            address: this.address,
            field: this.field,
            markerChangeAddress: this.markerChangeAddress,
            setMarkerChangeAddress: this.setMarkerChangeAddress,
            setAddress: this.setAddress,
            classes: this.classes
        })
    },
    created() {
        this.field = this.optField
        jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
            if (this.field.id === attribute.id) {
                this.address = attribute.value.address
                if (attribute.value && attribute.value.position && attribute.value.position.lat !== 0 && attribute.value.position.lng !== 0) {
                    this.position = attribute.value.position
                }
            }
        })
    },
    mounted() {
        let interval = setInterval(() => {
            if (typeof google !== 'undefined') {
                if (google.maps) {
                    clearInterval(interval)
                    this.initMap()
                }
            }
        }, 300);
    }
}
</script>



