<script>
    export default {
        name: "vehica-car-field-rewrite",
        data() {
            return {
                rewrite: '',
                showEditField: false,
            }
        },
        props: {
            field: Object,
            successText: String,
            errorText: String,
            inProgressText: String,
            requestUrl: String,
        },
        methods: {
            onShowEditField() {
                window.VehicaAdminEvents.$emit('showEditField', this.field.id)
                this.showEditField = true
            },
            setRewrite(rewrite) {
                this.rewrite = rewrite
            },
            onSave() {
                swal({
                    title: this.inProgressText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {fieldId: this.field.id, rewrite: this.rewrite},
                        success: (response) => {
                            if (!response || typeof response.success === 'undefined' || !response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                this.onCancel()
                                return
                            }

                            let field = this.field
                            field.rewrite = this.rewrite = response.rewrite
                            window.VehicaAdminEvents.$emit('vehicaCarFieldUpdated', field)
                            this.showEditField = false

                            window.VehicaAdminEvents.$emit('hideEditField', this.field.id)

                            window.history.replaceState({}, '', window.location.href)

                            swal({
                                title: this.successText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            this.showEditField = false

                            window.VehicaAdminEvents.$emit('hideEditField', this.field.id)

                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            onCancel() {
                this.rewrite = this.field.rewrite
                this.showEditField = false

                window.VehicaAdminEvents.$emit('hideEditField', this.field.id)
            }
        },
        render() {
            return this.$scopedSlots.default({
                rewrite: this.rewrite,
                setRewrite: this.setRewrite,
                showEditField: this.showEditField,
                onShowEditField: this.onShowEditField,
                onSave: this.onSave,
                onCancel: this.onCancel,
            })
        },
        created() {
            this.rewrite = this.field.rewrite

            window.VehicaAdminEvents.$on('showEditField', () => {
                if (this.showEditField) {
                    this.onCancel()
                }
            })
        }
    }
</script>



