<script>
    export default {
        name: "vehica-color-presets",
        props: {
            presets: {
                type: Array,
                default: () => {
                    return []
                }
            }
        },
        methods: {
            setPreset(index) {
                window.VehicaAdminEvents.$emit('setPreset', this.presets[index])
            }
        },
        render() {
            return this.$scopedSlots.default({
                setPreset: this.setPreset,
                presets: this.presets,
            })
        }
    }
</script>



