<script>
    export default {
        name: "vehica-car-field-name",
        data() {
            return {
                showEditField: false,
                name: ''
            }
        },
        props: {
            field: Object,
            requestUrl: String,
            somethingWentWrongText: String,
            savingChangesText: String,
        },
        methods: {
            onShowEditField() {
                window.VehicaAdminEvents.$emit('showEditField', this.field.id)
                this.showEditField = true
            },
            setName(name) {
                this.name = name
            },
            onSave() {
                swal({
                    title: this.savingChangesText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {fieldId: this.field.id, fieldName: this.name},
                        success: (response) => {
                            if (!response.success) {
                                swal({
                                    title: this.somethingWentWrongText,
                                    text: response.message,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            let field = this.field
                            field.name = this.name
                            window.VehicaAdminEvents.$emit('vehicaCarFieldUpdated', field)

                            this.showEditField = false

                            window.VehicaAdminEvents.$emit('hideEditField', this.field.id)

                            window.history.replaceState({}, '', window.location.href)

                            swal({
                                title: response.message,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            this.showEditField = false

                            window.VehicaAdminEvents.$emit('hideEditField', this.field.id)

                            swal({
                                title: this.somethingWentWrongText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            onCancel() {
                this.showEditField = false
                this.name = this.field.name

                window.VehicaAdminEvents.$emit('hideEditField', this.field.id)
            }
        },
        render() {
            return this.$scopedSlots.default({
                onSave: this.onSave,
                onCancel: this.onCancel,
                onShowEditField: this.onShowEditField,
                showEditField: this.showEditField,
                name: this.name,
                setName: this.setName,
            })
        },
        created() {
            this.name = this.field.name
            window.VehicaAdminEvents.$on('showEditField', () => {
                if (this.showEditField) {
                    this.onCancel()
                }
            })
        }
    }
</script>



