<script>
export default {
    name: "vehica-attachments",
    data() {
        return {
            ready: false,
            field: {},
            media: false,
            attachments: [],
            markedAttachments: []
        }
    },
    props: {
        optField: {
            type: Object
        },
        optFieldsUser: {
            type: Object
        },
        titleText: {
            type: String
        },
        buttonText: {
            type: String
        },
        validation: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        attachmentIds() {
            let attachmentIds = []

            jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
                if (attribute.id === this.field.id) {
                    attachmentIds = attribute.value
                }
            })

            return attachmentIds
        },
        classes() {
            if (!this.optField.isRequired) {
                return {}
            }

            return {
                'is-required': true,
                'is-empty': this.attachments.length === 0,
                'is-error': this.validation && this.attachments.length === 0
            }
        },
        value() {
            return this.attachments.map((attachment) => {
                return attachment.id
            }).join(',')
        },
        markedAttachmentsNumber() {
            return this.markedAttachments.length
        }
    },
    methods: {
        isAttachmentMarked(id) {
            return this.markedAttachments.indexOf(id) !== -1
        },
        markAttachment(id) {
            let index = this.markedAttachments.indexOf(id)
            if (index !== -1) {
                this.markedAttachments.splice(index, 1)
            } else {
                this.markedAttachments.push(id)
            }
        },
        removeMarkedAttachments() {
            this.images = this.images.filter((image) => {
                return this.markedAttachments.indexOf(image.id) === -1
            })
            this.markedAttachments.splice(0, this.markedAttachments.length)
        },
        moveAttachment(newIndex, oldIndex) {
            this.images.splice(newIndex + 1, 0, this.images.splice(oldIndex, 1)[0]);
        },
        removeAttachment(attachment) {
            let index = this.attachments.findIndex((img) => {
                return attachment.id === img.id
            })

            if (index !== -1) {
                this.attachments.splice(index, 1)
            }
        },
        openMedia() {
            if (this.media) {
                this.media.open()
                return
            }

            this.media = wp.media({
                title: this.titleText,
                button: {
                    text: this.buttonText
                },
                multiple: 'add',
            })
            this.media.on('open', () => {
                let selection = this.media.state().get('selection')
                this.attachments.forEach((attachment) => {
                    let att = wp.media.attachment(attachment.id)
                    att.fetch()

                    selection.add(att ? [att] : [])
                })
            })
            this.media.on('select', () => {
                this.media.state().get('selection').map((attachment) => {
                    return attachment.toJSON()
                }).filter((attachment) => {
                    if (!this.attachments.length) {
                        return true
                    }
                    return !this.attachments.some((att) => {
                        return attachment.id === att.id
                    })
                }).forEach((attachment) => {
                    this.attachments.push({
                        id: attachment.id,
                        filename: attachment.filename,
                        icon: attachment.icon,
                        url: attachment.url,
                        type: attachment.type,
                        subtype: attachment.subtype,
                    })
                })
            })
            this.media.open()
        }
    },
    render() {
        return this.$scopedSlots.default({
            field: this.field,
            fieldsUser: this.fieldsUser,
            openMedia: this.openMedia,
            attachments: this.attachments,
            moveAttachment: this.moveAttachment,
            markAttachment: this.markAttachment,
            removeMarkedAttachments: this.removeMarkedAttachments,
            markedAttachmentsNumber: this.markedAttachmentsNumber,
            isAttachmentMarked: this.isAttachmentMarked,
            value: this.value,
            key: this.field.key,
            classes: this.classes,
            removeAttachment: this.removeAttachment,
            ready: this.ready,
            imageIds: this.imageIds,
        })
    },
    created() {
        let attachments = []

        this.field = this.optField
        let count = 0
        let attachmentIds = []
        jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
            if (attribute.id === this.field.id) {
                count = attribute.value.length
                jQuery.each(attribute.value, (index, attachmentId) => {
                    attachmentIds.push(attachmentId)
                    wp.media.attachment(attachmentId).fetch().then(() => {
                        let attachment = wp.media.attachment(attachmentId).toJSON()

                        attachments.push({
                            id: attachment.id,
                            filename: attachment.filename,
                            icon: attachment.icon,
                            url: attachment.url,
                            type: attachment.type,
                            subtype: attachment.subtype,
                        })
                    })
                })
            }
        })

        let atts = []
        let interval = setInterval(() => {
            if (attachments.length === count) {
                attachmentIds.forEach((attachmentId) => {
                    let img = attachments.find((attachment) => {
                        return attachment.id === attachmentId
                    })
                    atts.push(img)
                })
                this.attachments = atts
                this.ready = true

                clearInterval(interval)
            }
        }, 500)
    }
}
</script>



