<script>
export default {
    name: "vehica-set-homepage",
    data() {
        return {
            pageId: false,
        }
    },
    props: {
        initialPageId: false,
    },
    methods: {
        setPage(pageId) {
            this.pageId = pageId
        }
    },
    render() {
        return this.$scopedSlots.default({
            setPage: this.setPage,
            pageId: this.pageId,
        })
    },
    created() {
        this.pageId = this.initialPageId
    }
}
</script>



