<script>
    export default {
        name: "vehica-car-fields",
        data() {
            return {
                fields: [],
                saveFieldsRequest: false
            }
        },
        props: {
            initialFields: {
                type: Array,
                default: () => {
                    return []
                }
            },
            saveFieldsRequestUrl: String,
            duplicateRequestUrl: String,
            duplicateInProgressText: String,
            duplicateSuccessText: String,
        },
        computed: {
            fieldsIds() {
                return this.fields.map((field) => {
                    return field.id
                })
            },
        },
        methods: {
            duplicate(field) {
                swal({
                    title: this.duplicateInProgressText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.duplicateRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {postId: field.id},
                        success: (response) => {
                            if (!response || !response.hasOwnProperty('success') || !response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }
                            window.history.replaceState({}, '', window.location.href)

                            this.fields.push(response.post)

                            swal({
                                title: this.duplicateSuccessText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            saveFields() {
                if (this.saveFieldsRequest) {
                    this.saveFieldsRequest.abort()
                }

                this.saveFieldsRequest = jQuery.ajax({
                    url: this.saveFieldsRequestUrl,
                    type: 'POST',
                    data: {fields: this.fieldsIds},
                    dataType: 'json',
                    success: () => {
                        window.history.replaceState({}, '', window.location.href)
                    }
                })
            },
        },
        render() {
            return this.$scopedSlots.default({
                fields: this.fields,
                createField: this.createField,
                duplicate: this.duplicate,
            })
        },
        created() {
            this.fields = this.initialFields

            window.VehicaAdminEvents.$on('vehicaCarFieldCreated', (field) => {
                this.fields.push(field)
            })

            window.VehicaAdminEvents.$on('vehicaCarFieldDeleted', (field) => {
                let index = this.fields.findIndex((f) => {
                    return f.id === field.id
                })

                if (index !== -1) {
                    this.fields.splice(index, 1)
                }
            })

            window.VehicaAdminEvents.$on('vehicaCarFieldUpdated', (field) => {
                let index = this.fields.findIndex((f) => {
                    return f.id === field.id
                })

                if (index !== -1) {
                    this.$set(this.fields, index, field)
                }
            })
        },
        watch: {
            fields() {
                this.saveFields()
            }
        }
    }
</script>



