<script>
export default {
    name: "vehica-gallery",
    data() {
        return {
            ready: false,
            field: {},
            media: false,
            images: [],
            markedImages: []
        }
    },
    props: {
        optField: {
            type: Object
        },
        optFieldsUser: {
            type: Object
        },
        titleText: {
            type: String
        },
        buttonText: {
            type: String
        },
        validation: {
            type: Boolean,
            default: false
        },
        requestUrl: {
            type: String,
            default: '',
        }
    },
    computed: {
        imageIds() {
            let imageIds = []

            jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
                if (attribute.id === this.field.id) {
                    imageIds = attribute.value
                }
            })

            return imageIds
        },
        classes() {
            if (!this.optField.isRequired) {
                return {}
            }

            return {
                'is-required': true,
                'is-empty': this.images.length === 0,
                'is-error': this.validation && this.images.length === 0
            }
        },
        value() {
            return this.images.map((image) => {
                return image.id
            }).join(',')
        },
        markedImagesNumber() {
            return this.markedImages.length
        }
    },
    methods: {
        isImageMarked(id) {
            return this.markedImages.indexOf(id) !== -1
        },
        markImage(id) {
            let index = this.markedImages.indexOf(id)
            if (index !== -1) {
                this.markedImages.splice(index, 1)
            } else {
                this.markedImages.push(id)
            }
        },
        removeMarkedImages() {
            this.images = this.images.filter((image) => {
                return this.markedImages.indexOf(image.id) === -1
            })
            this.markedImages.splice(0, this.markedImages.length)
        },
        moveImage(newIndex, oldIndex) {
            this.images.splice(newIndex + 1, 0, this.images.splice(oldIndex, 1)[0]);
        },
        removeImage(image) {
            let index = this.images.findIndex((img) => {
                return image.id === img.id
            })

            if (index !== -1) {
                this.images.splice(index, 1)
            }
        },
        openMedia() {
            if (this.media) {
                this.media.open()
                return
            }

            this.media = wp.media({
                title: this.titleText,
                button: {
                    text: this.buttonText
                },
                multiple: 'add',
                library: {
                    type: 'image'
                }
            })
            this.media.on('open', () => {
                let selection = this.media.state().get('selection')
                this.images.forEach((image) => {
                    let img = wp.media.attachment(image.id)
                    img.fetch()
                    selection.add(img ? [img] : [])
                })
            })
            this.media.on('select', () => {
                this.media.state().get('selection').map((image) => {
                    return image.toJSON()
                }).filter((image) => {
                    if (!this.images.length) {
                        return true
                    }
                    return !this.images.some((img) => {
                        return image.id === img.id
                    })
                }).forEach((image) => {
                    let url
                    if (typeof image.sizes['vehica_default_0.2'] !== 'undefined') {
                        url = image.sizes['vehica_default_0.2'].url
                    } else {
                        url = image.sizes.full.url
                    }

                    this.images.push({
                        id: image.id,
                        url: url
                    })
                })
            })
            this.media.open()
        },
        fetchImages() {
            jQuery.ajax({
                url: this.requestUrl,
                type: 'POST',
                dataType: 'json',
                data: {
                    fieldId: this.field.id,
                    carId: this.optFieldsUser.id
                },
                success: (response) => {
                    if (jQuery.isArray(response)) {
                        this.images = response
                    } else {
                        this.images = []
                    }

                    this.ready = true
                }
            })
        }
    },
    render() {
        return this.$scopedSlots.default({
            field: this.field,
            fieldsUser: this.fieldsUser,
            openMedia: this.openMedia,
            images: this.images,
            moveImage: this.moveImage,
            markImage: this.markImage,
            removeMarkedImages: this.removeMarkedImages,
            markedImagesNumber: this.markedImagesNumber,
            isImageMarked: this.isImageMarked,
            value: this.value,
            key: this.field.key,
            classes: this.classes,
            removeImage: this.removeImage,
            ready: this.ready,
            imageIds: this.imageIds,
        })
    },
    created() {
        this.field = this.optField

        this.fetchImages()
    }
}
</script>



