<script>
    export default {
        name: "vehica-update-car",
        methods: {
            onUpdateCar() {
                window.VehicaAdminEvents.$emit('updateCar')
            }
        },
        render() {
            return this.$scopedSlots.default({
                onUpdateCar: this.onUpdateCar,
            })
        }
    }
</script>



