<script>
    export default {
        name: "vehica-payment-packages",
        data() {
            return {
                packages: []
            }
        },
        props: {
            deleteRequestUrl: String,
            initialPackages: {
                type: Array,
                default: function () {
                    return []
                },
            }
        },
        methods: {
            onDelete(packageId) {
                let index = this.packages.findIndex((paymentPackage) => {
                    return paymentPackage.id === packageId
                })

                if (index === -1) {
                    return
                }

                this.packages.splice(index, 1)

                jQuery.ajax({
                    url: this.deleteRequestUrl,
                    type: 'POST',
                    data: {packageId: packageId},
                })
            },
            onEdit(index) {

            },
        },
        render() {
            return this.$scopedSlots.default({
                packages: this.packages,
                onEdit: this.onEdit,
                onDelete: this.onDelete,
            })
        },
        created() {
            this.packages = this.initialPackages

            window.VehicaAdminEvents.$on('packageCreated', (paymentPackage) => {
                this.packages.push(paymentPackage)
            })

            window.VehicaAdminEvents.$on('updatePackage', (paymentPackage) => {
                let index = this.packages.findIndex((pp) => {
                    return pp.id === paymentPackage.id
                })

                if (index === -1) {
                    return
                }

                this.$set(this.packages, index, paymentPackage)
            })
        }
    }
</script>



