<script>
const DEFAULT_ZOOM = 15;

export default {
    name: "vehica-map-settings",
    data() {
        return {
            isGoogleLoaded: false,
            position: false,
            mapType: 'roadmap',
            zoom: DEFAULT_ZOOM,
            map: false,
            marker: false,
        }
    },
    props: {
        initialPosition: Object,
        initialMapType: String,
        initialZoom: Number,
    },
    methods: {
        setZoom(zoom) {
            this.zoom = zoom

            if (this.map) {
                let zoom = parseInt(this.zoom) || DEFAULT_ZOOM
                this.map.setZoom(zoom)
            }
        },
        setMapType(mapType) {
            this.mapType = mapType
            if (this.map) {
                this.map.setMapTypeId(this.mapType);
            }
        },
        initMap() {
            this.map = new google.maps.Map(document.getElementById('vehica-map-position'), {
                center: this.position,
                zoom: this.zoom,
                fullscreenControl: false,
                mapTypeControl: false,
                streetViewControl: false,
            })
            this.map.setMapTypeId(this.mapType)
            google.maps.event.addListener(this.map, 'click', (event) => {
                this.position = {
                    lat: event.latLng.lat(),
                    lng: event.latLng.lng()
                }
                this.setMarker()
            })
            let textInput = document.getElementById('vehica-map-address')
            let autoComplete = new google.maps.places.Autocomplete(textInput)
            autoComplete.addListener('place_changed', () => {
                let place = autoComplete.getPlace()
                if (!place.geometry) {
                    return
                }
                this.position = {
                    lat: place.geometry.location.lat(),
                    lng: place.geometry.location.lng(),
                }
                this.setMarker()
            })

            textInput.addEventListener('keydown', (event) => {
                if (event.key === 'Enter') {
                    event.preventDefault()
                }
            })

            this.setMarker()
        },
        setMarker() {
            if (this.marker !== false) {
                this.marker.setMap(null)
            }
            this.marker = new google.maps.Marker({
                position: this.position,
                map: this.map
            })
            this.map.setCenter(this.position)
        }
    },
    render() {
        return this.$scopedSlots.default({
            isGoogleLoaded: this.isGoogleLoaded,
            mapType: this.mapType,
            setMapType: this.setMapType,
            zoom: this.zoom,
            setZoom: this.setZoom,
            position: this.position,
        })
    },
    created() {
        this.position = this.initialPosition
        this.zoom = this.initialZoom
        this.mapType = this.initialMapType
    },
    mounted() {
        let interval = setInterval(() => {
            if (typeof window.google !== 'undefined' && typeof window.google.maps !== 'undefined') {
                clearInterval(interval)
                this.isGoogleLoaded = true
                this.$nextTick(() => {
                    this.initMap()
                })
            }
        }, 500)
    }
}
</script>



