<script>
    export default {
        name: 'vehica-set-logo',
        data() {
            return {
                uploader: false,
                logo: false,
            }
        },
        props: {
            titleText: {
                type: String,
                default: 'Select or Upload Media Of Your Chosen Persuasion'
            },
            buttonText: {
                type: String,
                default: 'Use this media'
            },
            multiple: {
                type: Boolean,
                default: false
            },
            initialLogoId: {
                default: false
            }
        },
        computed: {
            logoId() {
                if (!this.logo) {
                    return false
                }

                return this.logo.id
            },
            logoUrl() {
                if (!this.logo) {
                    return false
                }

                return this.logo.sizes.full.url
            }
        },
        methods: {
            removeLogo() {
              this.logo = false
            },
            openUploader() {
                if (this.uploader) {
                    this.uploader.open()
                    return
                }

                this.uploader = wp.media({
                    title: this.titleText,
                    button: {
                        text: this.buttonText
                    },
                    multiple: this.multiple
                })

                this.uploader.on('select', () => {
                    this.logo = this.uploader.state().get('selection').first().toJSON()
                })

                this.uploader.open()
            }
        },
        render() {
            return this.$scopedSlots.default({
                openUploader: this.openUploader,
                logoId: this.logoId,
                logoUrl: this.logoUrl,
                remove: this.removeLogo,
            })
        },
        created() {
            if (this.initialLogoId !== false) {
                wp.media.attachment(this.initialLogoId).fetch().then(() => {
                    this.logo = wp.media.attachment(this.initialLogoId).toJSON()
                })
            }
        }
    }
</script>



