<script>
export default {
    name: "vehica-price",
    data() {
        return {
            value: '',
            field: {},
            initialValue: true,
        }
    },
    props: {
        priceKey: String,
        optField: {
            type: Object
        },
        optFieldsUser: {
            type: Object
        },
        validation: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        setInitialValue(value) {
            if (value === 0) {
                this.value = ''
                return
            }

            let newValue
            if (this.decimalSeparator !== '') {
                let oldValue = this.value
                newValue = value.toString().replace(new RegExp('[^0-9' + this.decimalSeparator + ']', 'g'), '')

                if (newValue.split(this.decimalSeparator).length === 3) {
                    newValue = oldValue
                }
            } else {
                newValue = value.toString().replace(new RegExp('[^0-9]', 'g'), '')
            }

            this.value = 0
            this.value = newValue
        },
        setValue(value) {
            let newValue
            if (this.decimalSeparator !== '') {
                let oldValue = this.value

                value = value.toString()
                if (this.initialValue) {
                    value = value.replace('.', this.decimalSeparator)
                }

                newValue = value.replace(new RegExp('[^0-9' + this.decimalSeparator + ']', 'g'), '')

                if (newValue.split(this.decimalSeparator).length === 3) {
                    newValue = oldValue
                }
            } else {
                newValue = value.toString().replace(new RegExp('[^0-9]', 'g'), '')
            }

            this.value = 0
            this.value = newValue
        },
    },
    computed: {
        thousandsSeparator() {
            if (!this.currency) {
                return ','
            }

            return this.currency.thousands_separator
        },
        decimalSeparator() {
            if (!this.currency) {
                return '.'
            }

            return this.currency.decimal_separator
        },
        currency() {
            let field = this.optField.fields.find((field) => {
                return field.key === this.priceKey
            })

            if (typeof field === 'undefined') {
                return false
            }

            return field.currency
        },
        realValue() {
            let value = this.value.toString()

            if (this.thousandsSeparator !== '') {
                value = value.split(this.thousandsSeparator).join('')
            }

            if (this.decimalSeparator !== '' && this.decimalSeparator !== '.') {
                value = value.replace(this.decimalSeparator, '.')
            }

            if (typeof value === 'undefined') {
                return 0
            }

            let numberValue
            if (this.optField.numberType === 'integer') {
                numberValue = parseInt(value)
            } else {
                numberValue = parseFloat(value)
            }

            return value
        },
        classes() {
            if (!this.optField.isRequired) {
                return {}
            }
            return {
                'is-required': true,
                'is-empty': this.realValue === 0 || this.realValue === '',
                'is-error': this.validation && (this.realValue === 0 || this.realValue === '')
            }
        },
    },
    render() {
        return this.$scopedSlots.default({
            id: this.optField.id,
            key: this.optField.key,
            fieldName: this.fieldName,
            value: this.value,
            setValue: this.setValue,
            name: this.optField.name,
            fields: this.optField.fields,
            classes: this.classes,
        })
    },
    created() {
        jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
            if (attribute.id === this.optField.id && typeof attribute.value[this.priceKey]) {
                this.setValue(attribute.value[this.priceKey])
                return false
            }
        })

        this.initialValue = false
    }
}
</script>



