<script>
    export default {
        name: "vehica-true-false",
        data() {
            return {
                isChecked: false
            }
        },
        props: {
            optField: {
                type: Object
            },
            optFieldsUser: {
                type: Object
            },
        },
        methods: {
            onClick() {
                this.isChecked = !this.isChecked
            }
        },
        render() {
            return this.$scopedSlots.default({
                onClick: this.onClick,
                isChecked: this.isChecked,
            })
        },
        created() {
            jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
                if (attribute.id === this.optField.id) {
                    this.isChecked = attribute.value
                    return false
                }
            })
        }
    }
</script>



