<script>
    const OPTION_TEMPLATE_TYPE = 'vehica_template_type'

    export default {
        name: "vehica-templates-manager",
        data() {
            return {
                type: 'block',
                templates: [],
                showModal: false,
                isLoading: true,
                isImporting: false,
                keyword: '',
                trackedOptions: [
                    'vehica_layout',
                    'vehica_background_accent',
                    'vehica_car_preview',
                    'vehica_user_preview',
                    'vehica_post_preview',
                ],
                intent: 0
            }
        },
        props: {
            context: String,
            sourceUrl: String
        },
        computed: {
            currentTemplates() {
                let templates = this.templates.filter((template) => {
                    return template.type === this.type
                })

                if (this.keyword.trim() !== '') {
                    templates = templates.filter((template) => {
                        return template.title.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1
                    })
                }

                return templates
            }
        },
        methods: {
            setIntent(intent) {
                this.intent = intent
            },
            setKeyword(keyword) {
                this.keyword = keyword
            },
            setType(type) {
                this.type = type
            },
            synchronize() {
                jQuery.getJSON(this.sourceUrl + '/wp-admin/admin-ajax.php?action=vehica_templates&context=' + this.context).done((data) => {
                    this.templates = data.templates
                    this.isLoading = false
                })
            },
            setShowModal() {
                if (this.isLoading) {
                    this.synchronize()
                }
                this.showModal = !this.showModal
            },
            importTemplate(id) {
                this.isImporting = true
                let template = this.templates.find((template) => {
                    return template.id === id
                })

                if (!template) {
                    this.isImporting = false
                    return
                }

                jQuery.ajax(ajaxurl, {
                    data: {
                        action: 'vehica_get_template_data',
                        templateId: id
                    },
                    dataType: 'JSON',
                    type: 'POST'
                }).success((content) => {
                    template.get = function (arg) {
                        return template[arg]
                    }

                    elementor.channels.data.trigger('template:before:insert', template);

                    let config = {}
                    if (this.intent > 1) {
                        config.at = this.intent
                    }
                    elementor.getPreviewView().addChildModel(content, config);
                    elementor.channels.data.trigger('template:after:insert', template);
                }).done(() => {
                    this.showModal = false
                    this.isImporting = false
                })
            },
            reloadPreview() {
                elementor.saver.saveEditor({
                    status: elementor.settings.page.model.get('post_status'),
                    onSuccess: () => {
                        elementor.once('preview:loaded', () => {
                            this.setPageSettings()
                        })
                        elementor.reloadPreview();
                    }
                })
            },
            reloadEditor() {
                elementor.saver.update({
                    onSuccess() {
                        window.location.reload()
                    }
                })
            },
            setPageSettings() {
                if (typeof elementor.panel === 'undefined') {
                    return false
                }
                elementor.getPanelView().setPage('page_settings')
                return true
            }
        },
        render() {
            return this.$scopedSlots.default({
                setType: this.setType,
                type: this.type,
                showModal: this.showModal,
                setShowModal: this.setShowModal,
                templates: this.currentTemplates,
                isLoading: this.isLoading,
                importTemplate: this.importTemplate,
                isImporting: this.isImporting,
                keyword: this.keyword,
                setKeyword: this.setKeyword,
            })
        },
        mounted() {
            let section = jQuery('#tmpl-elementor-add-section')
            if (section.length > 0) {
                let text = section.text()
                text = text.replace('<div class="elementor-add-section-drag-title', '<div class="elementor-add-section-area-button elementor-add-vehica-button" title="Vehica"></div></button><div class="elementor-add-section-drag-title')
                section.text(text)

                elementor.on('preview:loaded', () => {
                    let showModal = this.setShowModal
                    let setIntent = this.setIntent

                    jQuery(elementor.$previewContents[0].body).on("click", ".elementor-add-vehica-button", function () {
                        let n = jQuery(this).parents(".elementor-section-wrap").length > 0 ? jQuery(this).parents(".elementor-add-section").index() : -1;
                        if (n > 1) {
                            jQuery(this).parents(".elementor-add-section-inline").remove()
                        }

                        setIntent(n)
                        showModal()
                    })
                })
            }

            elementor.settings.page.addChangeCallback('vehica_layout', this.reloadEditor)

            this.trackedOptions.forEach((option) => {
                elementor.settings.page.addChangeCallback(option, this.reloadPreview)
            })
        }
    }
</script>



