<script>
export default {
    name: "vehica-car-terms",
    data() {
        return {
            value: '',
            terms: [],
        }
    },
    props: {
        createString: String,
        placeholder: String,
        taxonomy: {
            type: Object
        },
        post: {
            type: Object
        },
        validation: {
            type: Boolean,
            default: false
        },
        create: {
            type: Boolean,
            default: true,
        }
    },
    computed: {
        classes() {
            if (!this.taxonomy.isRequired) {
                return {}
            }
            let isEmpty
            if (Array.isArray(this.value)) {
                isEmpty = this.value === null || this.value.length === 0
            } else {
                isEmpty = this.value === null || this.value === ''
            }
            return {
                'is-required': true,
                'is-empty': isEmpty,
                'is-error': this.validation && isEmpty
            }
        }
    },
    methods: {
        initSelect() {
            let select = jQuery('#' + this.taxonomy.key).selectize({
                valueField: 'name',
                labelField: 'name',
                searchField: 'name',
                create: this.taxonomy.create,
                preload: 'focus',
                placeholder: this.placeholder,
                render: {
                    option_create: (data, escape) => {
                        return '<div class="create">' + this.createString + ' <strong>' + escape(data.input) + '</strong>&hellip;</div>';
                    }
                },
                load: (query, callback) => {
                    let url = ajaxurl + '?action=vehica_taxonomy_terms&taxonomy=' + this.taxonomy.key + '&term=' + query

                    jQuery.ajax({
                        url: url,
                        type: 'GET',
                        dataType: 'json',
                        error: () => {
                            callback();
                        },
                        success: (response) => {
                            callback(response)
                        }
                    })
                }
            })

            select.on('change', (event) => {
                let value = jQuery(event.currentTarget).val()
                if (value === null) {
                    value = ''
                }
                this.value = value

                let selected = jQuery(event.currentTarget).val()
                let terms = []
                if (typeof selected !== 'undefined' && selected !== null) {
                    jQuery.each(this.terms, (index, term) => {
                        if (selected.indexOf(term.name) !== -1) {
                            terms.push(term)
                        }
                    })
                }

                window.VehicaAdminEvents.$emit('taxonomyUpdated', {
                    taxonomy: this.taxonomy,
                    terms: terms
                })
            })

            let selectize = select[0].selectize;

            jQuery.each(this.post.attributes, (index, attribute) => {
                if (attribute.id === this.taxonomy.id) {
                    let options = []

                    jQuery.each(attribute.value, (index, term) => {
                        selectize.addOption({
                            name: term.name,
                        })

                        options.push(term.name)
                    })

                    this.value = options

                    selectize.setValue(options, true)
                    return false
                }
            })
        },
    },
    render() {
        return this.$scopedSlots.default({
            taxonomy: this.taxonomy,
            classes: this.classes
        })
    },
    created() {
        if (this.taxonomy.allowMultiple) {
            this.value = []
        }
    },
    mounted() {
        this.initSelect()
    },
}
</script>



