<script>
    export default {
        name: "vehica-currencies",
        data() {
            return {
                currencies: [],
                defaultCurrencyId: 0
            }
        },
        props: {
            successText: String,
            createRequestUrl: String,
            deleteRequestUrl: String,
            setDefaultRequestUrl: String,
            creatingCurrencyText: String,
            currencyCreatedText: String,
            inProgressText: String,
            errorText: String,
            initialCurrencies: {
                type: Array,
                default: () => {
                    return []
                }
            },
            initialDefaultCurrencyId: {
                type: Number,
                default: 0
            },
            areYouSureTitleText: String,
            areYouSureMsgText: String,
            confirmText: String,
            cancelText: String,
            deletingCurrencyText: String,
            currencyDeletedText: String,
        },
        methods: {
            setDefaultCurrency(currency) {
                this.defaultCurrencyId = currency.id

                swal({
                    title: this.inProgressText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.setDefaultRequestUrl,
                        type: 'POST',
                        data: {currencyId: currency.id},
                        dataType: 'json',
                        success: (response) => {
                            if (!response || !response.hasOwnProperty('success') || !response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            window.history.replaceState({}, '', window.location.href)

                            swal({
                                title: this.successText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        }
                    })
                }, 500)
            },
            onDeleteCurrency(currency) {
                swal.fire({
                    title: this.areYouSureTitleText,
                    text: this.areYouSureMsgText,
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: this.confirmText,
                    cancelButtonText: this.cancelText
                }).then((result) => {
                    if (result.value) {
                        this.deleteCurrency(currency)
                    }
                })
            },
            deleteCurrency(currency) {
                swal({
                    title: this.deletingCurrencyText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.deleteRequestUrl,
                        type: 'POST',
                        data: {currencyId: currency.id},
                        dataType: 'json',
                        success: (response) => {
                            if (!response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            let index = this.currencies.findIndex((curr) => {
                                return curr.id === currency.id
                            })

                            if (index === -1) {
                                return
                            }

                            this.currencies.splice(index, 1)

                            if (currency.id === this.defaultCurrencyId && this.currencies.length > 1) {
                                this.defaultCurrencyId = this.currencies[0].id
                            }

                            window.history.replaceState({}, '', window.location.href)

                            swal({
                                title: this.currencyDeletedText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            createCurrency() {
                swal({
                    title: this.creatingCurrencyText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.createRequestUrl,
                        type: 'POST',
                        dataType: 'json',
                        success: (response) => {
                            if (!response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            this.currencies.push(response.currency)

                            window.history.pushState({}, '', window.location.href)

                            swal({
                                title: this.currencyCreatedText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
        },
        render() {
            return this.$scopedSlots.default({
                currencies: this.currencies,
                setDefault: this.setDefaultCurrency,
                defaultCurrency: this.defaultCurrencyId,
                create: this.createCurrency,
                delete: this.onDeleteCurrency,
            })
        },
        created() {
            this.currencies = this.initialCurrencies
            this.defaultCurrencyId = this.initialDefaultCurrencyId

            window.VehicaAdminEvents.$on('currencyUpdated', (currency) => {
                let index = this.currencies.findIndex((curr) => {
                    return curr.id === currency.id
                })

                if (index === -1) {
                    return
                }

                this.$set(this.currencies, index, currency)
            })
        }
    }
</script>



