<script>
export default {
    name: "vehica-term-importer",
    data() {
        return {
            taxonomy: '0',
        }
    },
    methods: {
        setTaxonomy(taxonomy) {
            this.taxonomy = taxonomy
        }
    },
    render() {
        return this.$scopedSlots.default({
            taxonomy: this.taxonomy,
            setTaxonomy: this.setTaxonomy,
        })
    }
}
</script>



