<script>
export default {
    name: "vehica-connect-terms",
    data() {
        return {
            inProgress: false,
        }
    },
    props: {
        requestUrl: String,
    },
    render() {
        return this.$scopedSlots.default({
            onStart: this.onStart,
            inProgress: this.inProgress,
        })
    },
    methods: {
        onStart() {
            this.inProgress = true

            this.fetchIds()
        },
        fetchIds() {
            jQuery.ajax({
                url: this.requestUrl,
                type: 'POST',
                dataType: 'json',
                data: {
                    action: 'vehica_listing_ids',
                },
                success: (ids) => {
                    this.check(ids)
                }
            })
        },
        check(ids) {
            if (ids.length === 0) {
                this.inProgress = false

                return
            }

            let limit = 100
            if (ids.length < limit) {
                limit = ids.length
            }

            this.checkIds(ids.splice(0, limit), ids)
        },
        checkIds(checkIds, allIds) {
            jQuery.ajax({
                url: this.requestUrl,
                type: 'POST',
                dataType: 'json',
                data: {
                    action: 'vehica/terms/backendConnect',
                    ids: checkIds,
                },
                success: () => {
                    this.check(allIds)
                }
            })
        }
    }
}
</script>



