<script>
    export default {
        name: "vehica-car-field-type",
        data() {
            return {
                showEditField: false,
                type: ''
            }
        },
        props: {
            field: Object,
            fieldTypes: Array,
            requestUrl: String,
            savingChangesText: String,
            somethingWentWrongText: String,
            areYouSureText: String,
            warningText: String,
            cancelText: String,
            confirmText: String,
        },
        computed: {
            currentTypeName() {
                let currentType = this.fieldTypes.find((type) => {
                    return type.key === this.field.type
                })

                if (typeof currentType === 'undefined') {
                    return ''
                }

                return currentType.name
            }
        },
        methods: {
            onShowEditField() {
                window.VehicaAdminEvents.$emit('showEditField', this.field.id)
                this.showEditField = true
            },
            onSave() {
                swal.fire({
                    title: this.areYouSureText,
                    text: this.warningText,
                    type: 'warning',
                    showCancelButton: true,
                    confirmButtonText: this.confirmText,
                    cancelButtonText: this.cancelText
                }).then((result) => {
                    if (result.value) {
                        swal({
                            title: this.savingChangesText,
                            showConfirmButton: false,
                            showCloseButton: false,
                            showCancelButton: false,
                            allowEscapeKey: false,
                            allowOutsideClick: false,
                            type: 'info'
                        }).catch(swal.noop);
                        swal.showLoading();

                        setTimeout(() => {
                            jQuery.ajax({
                                url: this.requestUrl,
                                type: 'POST',
                                dataType: 'json',
                                data: {fieldId: this.field.id, fieldType: this.type},
                                success: (response) => {
                                    if (!response.success) {
                                        swal({
                                            title: this.somethingWentWrongText,
                                            text: response.message,
                                            type: 'error',
                                            showConfirmButton: true,
                                        }).catch(swal.noop)
                                        return
                                    }

                                    window.VehicaAdminEvents.$emit('vehicaCarFieldUpdated', response.field)

                                    this.showEditField = false

                                    window.VehicaAdminEvents.$emit('hideEditField', this.field.id)

                                    window.history.replaceState({}, '', window.location.href)

                                    swal({
                                        title: response.message,
                                        type: 'success',
                                        showConfirmButton: false,
                                        timer: 1500
                                    }).catch(swal.noop)
                                },
                                error: () => {
                                    this.showEditField = false

                                    window.VehicaAdminEvents.$emit('hideEditField', this.field.id)

                                    swal({
                                        title: this.somethingWentWrongText,
                                        type: 'error',
                                        showConfirmButton: true,
                                    }).catch(swal.noop)
                                }
                            })
                        }, 750)
                    }
                })
            },
            onCancel() {
                this.showEditField = false
                this.type = this.field.type

                window.VehicaAdminEvents.$emit('hideEditField', this.field.id)
            },
            setType(type) {
                this.type = type
            }
        },
        render() {
            return this.$scopedSlots.default({
                showEditField: this.showEditField,
                onShowEditField: this.onShowEditField,
                onSave: this.onSave,
                onCancel: this.onCancel,
                setType: this.setType,
                currentTypeName: this.currentTypeName,
            })
        },
        created() {
            this.type = this.field.type

            window.VehicaAdminEvents.$on('showEditField', () => {
                if (this.showEditField) {
                    this.onCancel()
                }
            })
        }
    }
</script>



