<script>
    export default {
        name: "vehica-template-name",
        data() {
            return {
                showEditField: false,
                name: ''
            }
        },
        props: {
            template: Object,
            requestUrl: String,
            savingChangesText: String,
            somethingWentWrongText: String,
            successText: String,
        },
        render() {
            return this.$scopedSlots.default({
                showEditField: this.showEditField,
                onShowEditField: this.onShowEditField,
                onSave: this.onSave,
                onCancel: this.onCancel,
                setName: this.setName,
                name: this.name,
            })
        },
        methods: {
            setName(name) {
                this.name = name
            },
            onShowEditField() {
                window.VehicaAdminEvents.$emit('showEditField', this.template.id)
                this.showEditField = true
            },
            onSave() {
                swal({
                    title: this.savingChangesText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                jQuery.ajax({
                    url: this.requestUrl,
                    type: 'POST',
                    dataType: 'json',
                    data: {templateId: this.template.id, templateName: this.name},
                    success: (response) => {
                        if (!response.success) {
                            swal({
                                title: this.somethingWentWrongText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                            return
                        }

                        let template = this.template
                        template.name = this.name
                        window.VehicaAdminEvents.$emit('mcTemplateUpdated', template)
                        this.showEditField = false

                        window.VehicaAdminEvents.$emit('hideEditField', this.template.id)

                        swal({
                            title: this.successText,
                            type: 'success',
                            showConfirmButton: false,
                            timer: 1500
                        }).catch(swal.noop)
                    },
                    error: () => {
                        this.showEditField = false

                        window.VehicaAdminEvents.$emit('hideEditField', this.template.id)

                        swal({
                            title: this.somethingWentWrongText,
                            type: 'error',
                            showConfirmButton: true,
                        }).catch(swal.noop)
                    }
                })
            },
            onCancel() {
                this.showEditField = false
                this.name = this.template.name

                window.VehicaAdminEvents.$emit('hideEditField', this.template.id)
            }
        },
        created() {
            this.name = this.template.name

            window.VehicaAdminEvents.$on('showEditField', () => {
                if (this.showEditField) {
                    this.onCancel()
                }
            })
        }
    }
</script>



