Vue.component('vehica-connect-terms', ConnectTerms)
Vue.component('vehica-color-presets', McColorPresets)
Vue.component('vehica-design-color', McDesignColor)
Vue.component('vehica-color', McColor)
Vue.component('date-picker', McDatePicker)
Vue.component('vehica-date-time-field', McDateTimeField)
Vue.component('vehica-text', McText)
Vue.component('vehica-attachments', McAttachments)
Vue.component('vehica-true-false', McTrueFalse)
Vue.component('vehica-number', McNumber)
Vue.component('vehica-price', McPrice)
Vue.component('vehica-gallery', McGallery)
Vue.component('vehica-location', McLocation)
Vue.component('vehica-embed', McEmbed)
Vue.component('vehica-image', McImage)
Vue.component('vehica-date-time', McDateTime)
Vue.component('vehica-template-settings', McTemplateSettings)
Vue.component('vehica-sections', McSections)
Vue.component('vehica-draggable', McDraggable)
Vue.component('vehica-car-terms', McCarTerms)
