Vue.component('vehica-set-currency-name', McSetCurrencyName)
Vue.component('vehica-set-currency-sign', McSetCurrencySign)
Vue.component('vehica-set-currency-sign-position', McSetCurrencySignPosition)
Vue.component('vehica-set-currency-format', McSetCurrencyFormat)
Vue.component('vehica-chrome-picker', Chrome)
Vue.component('vehica-map-settings', McMapSettings)
