Vue.component('vehica-set-homepage', McSetHomepage)

window.VehicaAdminEvents = new Vue()

const vue = document.getElementsByClassName('vehica-app')
Array.prototype.forEach.call(vue, function (el) {
    new Vue({
        el: el,
        created() {
            let elements = document.querySelectorAll('.vehica-v-hide');
            [].forEach.call(elements, function (el) {
                el.classList.remove('vehica-v-hide')
            });
        }
    })
});

jQuery(window).on('elementor:init', function () {
    new Vue({
        el: '#vehica-templates-manager',
    })
});

jQuery(document).ready(function () {
    jQuery('.vehica-selectize').selectize();
});

jQuery('.vehica-smooth-click').on('click', function (e) {
    e.preventDefault()

    ScrollToElement(jQuery(this).attr('href'), {
        ease: 'in-out-quad',
        duration: 1000
    })
})

if (jQuery('.vehica-button.vehica-hook').length > 0) {
    jQuery('.vehica-button.vehica-hook').on('click', function (e) {
        e.preventDefault()

        jQuery('#vehica-hook').val(jQuery(this).data('hook'))

        jQuery('form').submit()
    })
}

window.mapLoaded = function () {
}


