<script>
    export default {
        name: "vehica-submit-form",
        data() {
            return {
                selectedFields: [],
                availableFields: []
            }
        },
        props: {
            requestUrl: String,
            initialSelectedFields: Array,
            initialFields: Array,
            fields: Array,
        },
        computed: {
            selectedFieldKeys() {
                return this.selectedFields.map((field) => {
                    return field.key
                })
            }
        },
        methods: {
            onUpdate() {
                jQuery.ajax({
                    url: this.requestUrl,
                    type: 'POST',
                    data: {selectedFields: this.selectedFields},
                    dataType: 'json',
                    success: (response) => {

                    }
                })
            }
        },
        render() {
            return this.$scopedSlots.default({
                selectedFields: this.selectedFields,
                availableFields: this.availableFields,
                onUpdate: this.onUpdate,
            })
        },
        mounted() {
            this.selectedFields = this.initialSelectedFields
            this.availableFields = this.fields.filter((field) => {
                return this.selectedFieldKeys.indexOf(field.key) === -1
            })
        }
    }
</script>



