<script>
export default {
    name: "vehica-install-plugin",
    data() {
        return {
            inProgress: false,
        }
    },
    props: {
        plugin: String,
        requestUrl: String,
    },
    methods: {
        install() {
            this.inProgress = true

            jQuery.ajax({
                url: this.requestUrl,
                type: 'POST',
                data: {
                    plugin: this.plugin,
                },
                success: () => {
                    window.location.reload()
                }
            })
        }
    },
    render() {
        return this.$scopedSlots.default({
            install: this.install,
            inProgress: this.inProgress,
        })
    }
}
</script>



