<script>
export default {
    name: "vehica-demo-importer",
    data() {
        return {
            inProgress: false,
            state: false,
            demo: false,
            demoKey: '',
            steps: {
                posts: {normal: 30, loaded: 0},
                comments: {normal: 0, loaded: 0},
                users: {normal: 0, loaded: 0},
                media: {normal: 2, loaded: 0},
                terms: {normal: 100, loaded: 0},
                term_taxonomy: {normal: 100, loaded: 0},
                term_relationships: {normal: 100, loaded: 0},
                term_meta: {normal: 100, loaded: 0},
                options: {normal: 100, loaded: 0},
            },
            currentStep: 0
        }
    },
    computed: {
        showProgress() {
            return this.inProgress && this.progress !== false
        },
        progress() {
            if (!this.maxSteps) {
                return false
            }

            return Math.round(100 / this.maxSteps * this.currentStep)
        },
        maxSteps() {
            if (!this.demo) {
                return 0
            }

            let sum = 0
            jQuery.each(this.demo, (key, elements) => {
                if (typeof this.steps[key] !== 'undefined') {
                    let limit = this.steps[key].normal
                    if (limit === 0) {
                        sum += 1
                    } else {
                        sum += Math.ceil(elements / limit)
                    }
                }
            })

            return sum
        }
    },
    props: {
        requestUrl: String,
        demoUrl: String,
    },
    methods: {
        loadDemo() {
            jQuery.getJSON(this.demoUrl + '/' + this.demoKey + '/meta.json?time=' + new Date().getTime(), (data) => {
                this.demo = data
                this.prepare()
            })
        },
        onImport(demoKey) {
            this.demoKey = demoKey
            this.inProgress = true
            this.loadDemo()
        },
        prepare() {
            jQuery.ajax({
                method: 'POST',
                url: this.requestUrl,
                data: {action: 'vehica_importer_prepare', demoKey: this.demoKey}
            }).done(() => {
                this.nextStep('first')
            });
        },
        nextStep(lastStep) {
            let nextStep = false
            jQuery.each(this.steps, (stepKey) => {
                if (lastStep === 'first') {
                    this.step(stepKey, 0)
                    return false
                }

                if (nextStep === true) {
                    this.step(stepKey, 0)
                    return false
                }

                if (stepKey === lastStep && stepKey !== 'options') {
                    nextStep = true
                }
            })

            if (nextStep === false && lastStep !== 'first') {
                this.clearCache()
            }
        },
        step(key, start) {
            let data = {
                action: 'vehica_importer_' + key,
                demoKey: this.demoKey
            };

            let limit = this.steps[key]['normal'];
            if (limit === 0) {
                data.start = 0;
                data.limit = this.demo[key];
            } else {
                data.start = start;
                data.limit = start + limit;
                if (data.limit > this.demo[key]) {
                    data.limit = this.demo[key];
                }
            }

            jQuery.ajax({
                method: 'POST',
                url: this.requestUrl + '?key=' + key + '&time=' + new Date().getTime(),
                data: data
            }).done(() => {
                this.currentStep += 1

                this.steps[key].loaded = Math.round(data.limit / this.demo[key] * 100);
                if (data.limit < this.demo[key]) {
                    this.step(key, data.limit);
                } else {
                    this.nextStep(key);
                }
            }).fail(() => {
                this.timer = 30
                let timer = setInterval(() => {
                    if (this.timer > 0) {
                        this.timer--;
                    } else {
                        this.step(key, start)
                        clearInterval(timer)
                    }
                }, 1000)
            })
        },
        clearCache() {
            jQuery.ajax({
                method: 'POST',
                url: this.requestUrl,
                data: {action: 'vehica_importer_cache', demoKey: this.demoKey}
            }).done(() => {
                this.state = 'demoLoaded'
                window.location.href = window.location.href + '&state=demoLoaded'
            })
        },
    },
    render() {
        return this.$scopedSlots.default({
            onImport: this.onImport,
            maxSteps: this.maxSteps,
            currentStep: this.currentStep,
            progress: this.progress,
            inProgress: this.inProgress,
            showProgress: this.showProgress,
        })
    }
}
</script>



