<script>
export default {
    name: "vehica-create-payment-package",
    data() {
        return {
            name: '',
            label: '',
            price: '',
            displayPrice: '',
            number: 10,
            expire: 30,
            featuredExpire: 7,
            inProgress: false,
        }
    },
    props: {
        vehicaNonce: String,
        requestUrl: String,
    },
    methods: {
        onCreate() {
            this.inProgress = true

            jQuery.ajax({
                url: this.requestUrl,
                type: 'POST',
                dataType: 'json',
                data: {
                    package: {
                        name: this.name,
                        label: this.label,
                        price: this.price,
                        displayPrice: this.displayPrice,
                        number: this.number,
                        expire: this.expire,
                        featuredExpire: this.featuredExpire,
                    },
                    nonce: this.vehicaNonce,
                },
                success: (response) => {
                    this.inProgress = false

                    if (typeof response.success === 'undefined' || response.success !== true) {
                        return
                    }

                    window.VehicaAdminEvents.$emit('packageCreated', response.package)
                }
            })
        },
        setName(name) {
            this.name = name
        },
        setLabel(label) {
            this.label = label
        },
        setPrice(price) {
            this.price = price
        },
        setDisplayPrice(displayPrice) {
            this.displayPrice = displayPrice
        },
        setNumber(number) {
            this.number = number
        },
        setExpire(expire) {
            this.expire = expire
        },
        setFeaturedExpire(featuredExpire) {
            this.featuredExpire = featuredExpire
        },
    },
    render() {
        return this.$scopedSlots.default({
            onCreate: this.onCreate,
            name: this.name,
            label: this.label,
            price: this.price,
            displayPrice: this.displayPrice,
            number: this.number,
            expire: this.expire,
            featuredExpire: this.featuredExpire,
            setName: this.setName,
            setLabel: this.setLabel,
            setPrice: this.setPrice,
            setDisplayPrice: this.setDisplayPrice,
            setNumber: this.setNumber,
            setExpire: this.setExpire,
            setFeaturedExpire: this.setFeaturedExpire,
        })
    }
}
</script>



