<script>
    export default {
        name: "vehica-set-currency-sign",
        data() {
            return {
                showEditField: false,
                sign: '',
            }
        },
        props: {
            currency: Object,
            requestUrl: String,
            successText: String,
            inProgressText: String,
            errorText: String,
        },
        methods: {
            setSign(sign) {
                this.sign = sign
            },
            onError() {
                this.onCancel()

                swal({
                    title: this.errorText,
                    type: 'error',
                    showConfirmButton: true,
                }).catch(swal.noop)
            },
            onSave() {
                swal({
                    title: this.inProgressText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        data: {currencyId: this.currency.id, currencySign: this.sign},
                        dataType: 'json',
                        success: (response) => {
                            if (!response || !response.hasOwnProperty('success') || !response.success) {
                                this.onError()
                                return
                            }

                            this.showEditField = false
                            let currency = this.currency
                            currency.sign = this.sign
                            window.VehicaAdminEvents.$emit('currencyUpdated', currency)

                            window.VehicaAdminEvents.$emit('hideEditField', this.currency.id)

                            swal({
                                title: this.successText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            this.onError()
                        }
                    })
                }, 750)
            },
            onCancel() {
                this.showEditField = false
                this.sign = this.currency.sign

                window.VehicaAdminEvents.$emit('hideEditField', this.currency.id)
            },
            onShowEditField() {
                window.VehicaAdminEvents.$emit('showEditField', this.currency.id)
                this.showEditField = true
            }
        },
        render() {
            return this.$scopedSlots.default({
                sign: this.sign,
                setSign: this.setSign,
                onSave: this.onSave,
                onCancel: this.onCancel,
                showEditField: this.showEditField,
                onShowEditField: this.onShowEditField,
            })
        },
        created() {
            this.sign = this.currency.sign

            window.VehicaAdminEvents.$on('showEditField', () => {
                if (this.showEditField) {
                    this.onCancel()
                }
            })
        }
    }
</script>



