<script>
    export default {
        name: "vehica-text",
        data() {
            return {
                value: '',
                field: {},
            }
        },
        props: {
            optField: {
                type: Object
            },
            optFieldsUser: {
                type: Object
            },
            validation: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            classes() {
                if (!this.optField.isRequired) {
                    return {}
                }
                return {
                    'is-required': true,
                    'is-empty': this.value.length === 0,
                    'is-error': this.validation && this.value.length === 0
                }
            },
            placeholder() {
                return this.optField.name
            }
        },
        methods: {
            setValue(value) {
                this.value = value
            }
        },
        render() {
            return this.$scopedSlots.default({
                id: this.optField.id,
                key: this.optField.key,
                value: this.value,
                setValue: this.setValue,
                placeholder: this.placeholder,
                name: this.optField.name,
                classes: this.classes
            })
        },
        created() {
            jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
                if (attribute.id === this.optField.id) {
                    this.value = attribute.value
                    return false
                }
            })

            window.VehicaAdminEvents.$on('validation', (validation) => {
                this.validation = validation
            })
        }
    }
</script>



