<script>
    export default {
        name: "vehica-set-currency-format",
        data() {
            return {
                format: '',
                showEditField: false,
            }
        },
        props: {
            formats: Object,
            currency: Object,
            requestUrl: String,
            successText: String,
            inProgressText: String,
            errorText: String,
        },
        computed: {
            displayFormat() {
                let output = ''

                jQuery.each(this.formats, (format, displayFormat) => {
                    if (format === this.format) {
                        output = displayFormat
                        return false
                    }
                })

                return output
            }
        },
        methods: {
            onError() {
                this.onCancel()

                swal({
                    title: this.errorText,
                    type: 'error',
                    showConfirmButton: true,
                }).catch(swal.noop)
            },
            onShowEditField() {
                window.VehicaAdminEvents.$emit('showEditField', this.currency.id)
                this.showEditField = true
            },
            setFormat(format) {
                this.format = format
            },
            onSave() {
                swal({
                    title: this.inProgressText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        data: {currencyId: this.currency.id, currencyFormat: this.format},
                        dataType: 'json',
                        success: (response) => {
                            if (!response || !response.hasOwnProperty('success') || !response.success) {
                                this.onError()
                                return
                            }

                            this.showEditField = false
                            let currency = this.currency
                            currency.format = this.format
                            window.VehicaAdminEvents.$emit('currencyUpdated', currency)

                            window.VehicaAdminEvents.$emit('hideEditField', this.currency.id)

                            swal({
                                title: this.successText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            this.onError()
                        }
                    })
                }, 750)
            },
            onCancel() {
                this.showEditField = false
                this.format = this.currency.format

                window.VehicaAdminEvents.$emit('hideEditField', this.currency.id)
            }
        },
        render() {
            return this.$scopedSlots.default({
                format: this.format,
                setFormat: this.setFormat,
                onShowEditField: this.onShowEditField,
                showEditField: this.showEditField,
                onSave: this.onSave,
                onCancel: this.onCancel,
                displayFormat: this.displayFormat,
            })
        },
        created() {
            this.format = this.currency.format

            window.VehicaAdminEvents.$on('showEditField', () => {
                if (this.showEditField) {
                    this.onCancel()
                }
            })
        }
    }
</script>



