<script>
    export default {
        name: "vehica-embed",
        data() {
            return {
                url: '',
                embed: '',
                loading: false
            }
        },
        props: {
            optField: {
                type: Object
            },
            optFieldsUser: {
                type: Object
            },
            validation: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            classes() {
                if (!this.optField.isRequired) {
                    return {}
                }
                return {
                    'is-required': true,
                    'is-empty': this.url.length === 0,
                    'is-error': this.validation && this.url.length === 0
                }
            },
        },
        methods: {
            setUrl(url) {
                this.url = url
                this.setEmbed()
            },
            setEmbed() {
                this.loading = true
                jQuery.ajax(ajaxurl, {
                    data: {
                        url: this.url,
                        action: 'vehica_embed_preview',
                        fieldId: this.optField.id
                    },
                    type: 'POST'
                }).done((embed) => {
                    this.embed = embed
                }).fail(() => [
                    this.embed = ''
                ]).always(() => {
                    this.loading = false
                })
            }
        },
        render() {
            return this.$scopedSlots.default({
                field: this.optField,
                url: this.url,
                embed: this.embed,
                loading: this.loading,
                setUrl: this.setUrl,
                classes: this.classes,
            })
        },
        created() {
            jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
                if (attribute.id === this.optField.id) {
                    if (typeof attribute.value.url !== 'undefined') {
                        this.url = attribute.value.url
                    }
                    this.embed = attribute.displayValue
                    return false
                }
            })
        }
    }
</script>



