<script>
    export default {
        name: "vehica-create-car-field",
        data() {
            return {
                inProgress: false,
                fieldName: '',
                fieldType: '',
            }
        },
        props: {
            creatingNewFieldText: {
                default: 'Creating new field',
                type: String
            },
            successText: {
                default: '',
                type: String,
            },
            errorText: {
                default: 'Something went wrong',
                type: String
            },
            requestUrl: String,
        },
        methods: {
            setFieldName(name) {
                this.fieldName = name
            },
            setFieldType(type) {
                this.fieldType = type
            },
            createField() {
                this.inProgress = true

                swal({
                    title: this.creatingNewFieldText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {
                            fieldName: this.fieldName,
                            fieldType: this.fieldType,
                        },
                        success: (response) => {
                            this.inProgress = false

                            this.fieldName = ''
                            this.fieldType = ''

                            if (!response.success) {
                                swal({
                                    title: response.message,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            window.VehicaAdminEvents.$emit('vehicaCarFieldCreated', response.field)

                            window.history.replaceState({}, '', window.location.href)

                            swal({
                                title: response.message,
                                text: this.successText,
                                type: 'success',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        },
                        error: () => {
                            this.inProgress = false

                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            }
        },
        render() {
            return this.$scopedSlots.default({
                inProgress: this.inProgress,
                createField: this.createField,
                setFieldName: this.setFieldName,
                fieldName: this.fieldName,
                setFieldType: this.setFieldType,
                fieldType: this.fieldType,
            })
        }
    }
</script>



