<script>
export default {
    name: "vehica-date-time-field",
    data() {
        return {
            isRange: false,
            type: 'datetime',
            weekStart: 1,
            value: '',
        }
    },
    props: {
        optIsRange: {
            type: Boolean,
            default: false
        },
        optType: {
            type: String,
            default: 'datetime'
        },
        optWeekStart: {
            type: Number,
            default: 1
        }
    },
    computed: {
        format() {
            let type = this.type
            if (type === 'datetime') {
                return 'YYYY-MM-DD HH:mm'
            } else if (type === 'date') {
                return 'YYYY-MM-DD'
            } else if (type === 'time') {
                return 'HH:mm'
            } else if (type === 'year') {
                return 'YYYY'
            } else if (type === 'month') {
                return 'mm'
            }
            return 'YYYY-MM-DD HH:mm'
        }
    },
    methods: {
        setIsRange() {
            this.isRange = !this.isRange
            if (this.isRange) {
                this.value = ['', '']
            } else {
                this.value = ''
            }
        },
        setType(type) {
            this.type = type
        },
        setWeekStart(weekStart) {
            this.weekStart = parseInt(weekStart)
        },
        setValue(value) {
            this.value = value
        },
    },
    render() {
        return this.$scopedSlots.default({
            type: this.type,
            isRange: this.isRange,
            range: this.isRange ? true : false,
            weekStart: this.weekStart,
            value: this.value,
            format: this.format,
            setIsRange: this.setIsRange,
            setType: this.setType,
            setWeekStart: this.setWeekStart,
            setValue: this.setValue
        });
    },
    created() {
        this.type = this.optType
        this.isRange = this.optIsRange
        this.weekStart = this.optWeekStart
    }
}
</script>



