<script>
export default {
    name: "vehica-set-location",
    data() {
        return {
            map: false,
            marker: false,
            address: '',
            location: false,
            syncAddress: true,
            geoCoder: false,
        }
    },
    props: {
        mapType: String,
        initialAddress: String,
        initialZoom: Number,
        initialPosition: Object,
        initialLocation: {
            default: false
        },
        snazzy: Boolean,
    },
    computed: {
        showResetButton() {
            return this.location !== false || this.address !== ''
        }
    },
    methods: {
        onReset() {
            this.address = ''
            this.location = false

            if (this.marker !== false) {
                this.marker.setMap(null)
            }

            this.map.setCenter(this.initialPosition)
        },
        onSyncAddress() {
            this.syncAddress = !this.syncAddress
        },
        initMap() {
            this.map = new google.maps.Map(document.getElementById('vehica-user-map'), {
                center: this.initialPosition,
                zoom: this.initialZoom
            })

            if (this.snazzy && typeof window.VehicaSnazzy !== 'undefined') {
                this.map.mapTypes.set('styled_map', new google.maps.StyledMapType(window.VehicaSnazzy))
                this.map.setMapTypeId('styled_map')
            }

            google.maps.event.addListener(this.map, 'click', (event) => {
                this.location = {
                    lat: event.latLng.lat(),
                    lng: event.latLng.lng()
                }

                if (!this.geoCoder) {
                    this.geoCoder = new google.maps.Geocoder()
                }

                if (this.syncAddress) {
                    this.geoCoder.geocode({
                        'latLng': event.latLng
                    }, (results, status) => {
                        if (status === google.maps.GeocoderStatus.OK && results[0]) {
                            this.address = results[0].formatted_address
                        }
                    })
                }

                this.setMarker()
            })

            let textInput = document.getElementById('vehica-user-address')
            let autoComplete = new google.maps.places.Autocomplete(textInput)
            autoComplete.addListener('place_changed', () => {
                let place = autoComplete.getPlace()
                if (!place.geometry) {
                    return
                }

                if (typeof place.formatted_address !== 'undefined') {
                    this.address = place.formatted_address
                }

                this.location = {
                    lat: place.geometry.location.lat(),
                    lng: place.geometry.location.lng(),
                }

                this.setMarker()
                this.map.setCenter(this.location)
            })

            textInput.addEventListener('keydown', (event) => {
                if (event.key === 'Enter') {
                    event.preventDefault()
                }
            })

            if (this.location) {
                this.setMarker()
                this.map.setCenter(this.location)
            }
        },
        setMarker() {
            if (this.marker !== false) {
                this.marker.setMap(null)
            }

            this.marker = new google.maps.Marker({
                position: this.location,
                map: this.map
            })
        },
        setAddress(address) {
            this.address = address
        }
    },
    render() {
        return this.$scopedSlots.default({
            address: this.address,
            location: this.location,
            onSyncAddress: this.onSyncAddress,
            syncAddress: this.syncAddress,
            setAddress: this.setAddress,
            onReset: this.onReset,
            showResetButton: this.showResetButton,
        })
    },
    mounted() {
        this.address = this.initialAddress
        this.location = this.initialLocation

        let interval = setInterval(() => {
            if (typeof window.google.maps !== 'undefined') {
                clearInterval(interval)
                this.initMap()
            }
        }, 300);
    },
}
</script>



