<script>
export default {
    name: "vehica-date-time",
    data() {
        return {
            value: null,
        }
    },
    props: {
        optField: {
            type: Object
        },
        optFieldsUser: {
            type: Object
        },
        validation: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        valueFrom() {
            if (!this.optField.isRange || jQuery.isArray(this.value) && this.value.length < 1) {
                return null
            }
            return typeof this.value[0] !== 'undefined' ? this.value[0] : null
        },
        valueTo() {
            if (!this.optField.isRange || jQuery.isArray(this.value) && this.value.length < 2) {
                return null
            }
            return typeof this.value[1] !== 'undefined' ? this.value[1] : null
        },
        isEmpty() {
            return (this.optField.isRange && (this.value.length < 2 || this.value[0] === null || this.value[1] === null))
                || (!this.optField.isRange && (typeof this.value === 'undefined' || this.value === null || this.value.length === 0))
        },
        format() {
            let type = this.optField.valueType
            if (type === 'datetime') {
                return 'YYYY-MM-DD HH:mm:ss'
            } else if (type === 'date') {
                return 'YYYY-MM-DD'
            } else if (type === 'time') {
                return 'HH:mm:ss'
            } else if (type === 'year') {
                return 'YYYY'
            } else if (type === 'month') {
                return 'MM'
            }
            return 'YYYY-MM-DD HH:mm:ss'
        },
        classes() {
            if (!this.optField.isRequired) {
                return {}
            }
            return {
                'is-required': true,
                'is-empty': this.isEmpty,
                'is-error': this.validation && this.isEmpty
            }
        },
    },
    methods: {
        setInitialValue(value) {
            if (this.optField.isRange && jQuery.isArray(value) && value.length === 2) {
                this.value = []
                this.value[0] = this.convertValue(value[0])
                this.value[1] = this.convertValue(value[1])
            } else if (!this.optField.isRange) {
                this.value = this.convertValue(value)
            }
        },
        convertValue(value) {
            if (value === '') {
                return null
            }

            let output = new Date(value)

            return output.toString()
        },
        setValue(value) {
            this.value = value
        }
    },
    render() {
        return this.$scopedSlots.default({
            field: this.optField,
            value: this.value,
            classes: this.classes,
            setValue: this.setValue,
            format: this.format,
            valueFrom: this.valueFrom,
            valueTo: this.valueTo
        })
    },
    created() {
        jQuery.each(this.optFieldsUser.attributes, (index, attribute) => {
            if (attribute.id === this.optField.id) {
                this.setInitialValue(attribute.value)
                return false
            }
        })

        if (this.optField.isRange && this.value === '') {
            this.value = []
        }
    }
}
</script>



