<script>
    export default {
        name: "vehica-row",
        data() {
            return {
                isEdited: false,
            }
        },
        props: {
            rowId: Number,
        },
        methods: {
            setIsEdited(isEdited) {
                this.isEdited = isEdited
            }
        },
        render() {
            return this.$scopedSlots.default({
                isEdited: this.isEdited,
            })
        },
        created() {
            window.VehicaAdminEvents.$on('showEditField', (id) => {
                if (this.rowId === id) {
                    this.$nextTick(() => {
                        this.isEdited = true
                    })
                }
            })

            window.VehicaAdminEvents.$on('hideEditField', (id) => {
                if (this.rowId === id) {
                    this.isEdited = false
                }
            })
        }
    }
</script>



