<script>
    export default {
        name: "vehica-design-color",
        data() {
            return {
                color: '',
                currentColor: '',
                showPicker: false
            }
        },
        props: {
            type: String,
            initialColor: {
                type: String,
                default: ''
            }
        },
        methods: {
            setColor(color) {
                this.color = color
                this.currentColor = color
            },
            setCurrentColor(currentColor) {
                this.currentColor = currentColor.hex
            },
            onShowPicker() {
                this.showPicker = true

                this.$nextTick(() => {
                    setTimeout(() => {
                        jQuery(document).on('click', this.checkOutsideClick)
                    }, 300)
                })
            },
            checkOutsideClick(event) {
                let target = jQuery(event.target)
                if (!target.closest('.vehica-color__sample__window').length) {
                    this.onCancel()
                }
            },
            onSave() {
                this.color = this.currentColor
                this.showPicker = false
                jQuery(document).unbind('click', this.checkOutsideClick)
            },
            onCancel() {
                this.currentColor = this.color
                this.showPicker = false
                jQuery(document).unbind('click', this.checkOutsideClick)
            }
        },
        render() {
            return this.$scopedSlots.default({
                color: this.color,
                setColor: this.setColor,
                onSave: this.onSave,
                onCancel: this.onCancel,
                onShowPicker: this.onShowPicker,
                showPicker: this.showPicker,
                setCurrentColor: this.setCurrentColor,
                currentColor: this.currentColor,
            })
        },
        created() {
            this.color = this.initialColor
            this.currentColor = this.initialColor

            window.VehicaAdminEvents.$on('setPreset', (preset) => {
                this.color = preset[this.type]
                this.currentColor = preset[this.type]
            })
        },
    }
</script>



