<script>
    export default {
        name: "vehica-template-type-settings",
        data() {
            return {
                previewId: 0,
                oldPreviewId: 0,
                initiation: true,
            }
        },
        props: {
            templateType: String,
            initialPreviewId: {
                type: Number,
                default: 0
            },
            initialPreviewName: String,
            apiEndpoint: String,
            requestUrl: String,
            savingChangesText: String,
            errorText: String,
            successText: String,
            notSetText: String,
            layouts: {
                type: Array,
                default: () => {
                    return []
                }
            },
            somethingWentWrongText: String,
        },
        computed: {
            isChanged() {
                return this.previewId !== this.oldPreviewId
            },
        },
        methods: {
            onSave() {
                swal({
                    title: this.savingChangesText,
                    showConfirmButton: false,
                    showCloseButton: false,
                    showCancelButton: false,
                    allowEscapeKey: false,
                    allowOutsideClick: false,
                    type: 'info'
                }).catch(swal.noop);
                swal.showLoading();

                setTimeout(() => {
                    jQuery.ajax({
                        url: this.requestUrl,
                        type: 'POST',
                        dataType: 'json',
                        data: {
                            previewId: this.previewId,
                            templateType: this.templateType,
                        },
                        success: (response) => {
                            if (!response.success) {
                                swal({
                                    title: this.errorText,
                                    type: 'error',
                                    showConfirmButton: true,
                                }).catch(swal.noop)
                                return
                            }

                            swal({
                                title: this.successText,
                                type: 'success',
                                showConfirmButton: false,
                                timer: 1500
                            }).catch(swal.noop)
                        },
                        error: () => {
                            swal({
                                title: this.errorText,
                                type: 'error',
                                showConfirmButton: true,
                            }).catch(swal.noop)
                        }
                    })
                }, 750)
            },
            initSelectPreview() {
                let select = jQuery(this.$el).find('.vehica-set-preview-selectize').selectize({
                    valueField: 'id',
                    labelField: 'name',
                    searchField: 'name',
                    preload: true,
                    load: (query, callback) => {
                        let url = this.apiEndpoint + '?search=' + query

                        jQuery.ajax({
                            url: url,
                            type: 'GET',
                            dataType: 'json',
                            error: () => {
                                callback();
                            },
                            success: (response) => {
                                let items = response.map((item) => {
                                    let name = ''

                                    if (typeof item.title !== 'undefined' && typeof item.title.rendered !== 'undefined') {
                                        name = item.title.rendered;
                                    } else if (typeof item.name !== 'undefined') {
                                        name = item.name;
                                    }

                                    if (name === '') {
                                        name = '(no name)'
                                    }

                                    return {
                                        id: item.id,
                                        name: name
                                    }
                                })

                                callback(items)
                            }
                        })
                    },
                })

                select.on('change', (event) => {
                    this.previewId = parseInt(event.target.value)

                    if (!this.initiation) {
                        this.onSave()
                    }
                })

                if (this.initialPreviewId) {
                    let selectize = select[0].selectize;

                    selectize.addOption({
                        id: this.initialPreviewId.toString(),
                        name: this.initialPreviewName
                    })

                    selectize.setValue(this.initialPreviewId.toString(), true)
                }
            }
        },
        render() {
            return this.$scopedSlots.default({
                isChanged: this.isChanged,
                onSave: this.onSave,
                previewId: this.previewId,
            })
        },
        created() {
            this.previewId = this.initialPreviewId
            this.oldPreviewId = this.initialPreviewId
        },
        mounted() {
            this.initSelectPreview()

            this.$nextTick(() => {
                this.initiation = false
            })
            return
            let select = jQuery(this.$el).find('.vehica-select2').select2({
                ajax: {
                    url: this.apiEndpoint,
                    dataType: 'json',
                    data: (params) => {
                        return {
                            search: params.term,
                        };
                    },
                    processResults: (data) => {
                        return {
                            results: data.map((item) => {
                                let text = ''

                                if (typeof item.title !== 'undefined' && typeof item.title.rendered !== 'undefined') {
                                    text = item.title.rendered;
                                } else if (typeof item.name !== 'undefined') {
                                    text = item.name;
                                }

                                if (text === '') {
                                    text = '(no name)'
                                }

                                return {
                                    id: item.id,
                                    text: text
                                }
                            })
                        };
                    }
                }
            }).on('change', (e) => {
                this.previewId = parseInt(e.target.value)

                if (!this.initiation) {
                    this.onSave()
                }
            })

            let option
            if (this.previewId) {
                option = new Option(this.initialPreviewName, this.initialPreviewId.toString(), true, true);
            } else {
                option = new Option(this.notSetText, '0', true, true);
            }

            select.append(option);
            select.trigger('change');

            this.$nextTick(() => {
                this.initiation = false
            })
        },
    }
</script>



